<template>
  <div class="modal fade" tabindex="-1" role="dialog" id="pricesPdfViewModal">
    <div class="modal-dialog modal-lg modal-dialog-centered modal-dialog-scrollable">
      <div class="modal-content">
        
        <div class="modal-body">
          <div class="row" v-if="the_prices_content.length>0">
            <div class="col-sm-12 text-center fs-12 mt-2">
              <pdf-header>
                报价单
              </pdf-header>
            </div>

            <div class="col-sm-12">
              <table class="pdf-table-outter">
                <tr>
                  <td width="70%">
                    <pdf-price-member :member="the_member"></pdf-price-member>
                  </td>
                  <td>
                    <pdf-price-msg :member="the_member" :pricesTime="the_prices_timed" :pricesNum="the_prices_numed"></pdf-price-msg>
                  </td>
                </tr>
              </table>
            </div>

            <div class="col-sm-12">
              <pdf-prices-freight :freights="the_prices_content" v-if="the_prices_id==null"></pdf-prices-freight>
              <pdf-prices-freight :freights="content" v-else></pdf-prices-freight>
            </div>

          </div>
          
          <pdf-footer :member="the_member" v-if="the_member!=null"></pdf-footer>
        </div>

        <div class="modal-footer">
          <button class="btn btn-outline-secondary btn-sm" data-dismiss="modal">关闭</button>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import service from '@/service'
import tool from '@/common/es/tool'
import PdfFooter from '@/components/Pdf/PdfFooter.vue'

import PdfHeader from '@/components/Pdf/PdfHeader.vue'
import PdfPriceMember from '@/components/Pdf/Prices/PdfPriceMember.vue'
import PdfPriceMsg from '@/components/Pdf/Prices/PdfPriceMsg.vue'
import PdfPricesFreight from '@/components/Pdf/Prices/PdfPricesFreight.vue'

export default {
  components: { 
    PdfFooter,
    PdfHeader,
    PdfPriceMember,
    PdfPriceMsg,
    PdfPricesFreight
  },
  name: 'PricesPdf',
  props: {
    the_prices_content: {
      type: Array
    },
    the_member: {
      type: Object
    },
    the_prices_timed: {
      type: String
    },
    the_prices_numed: {
      type: String
    },
    the_prices_id: {
      type: Number
    },
  },
  mounted() {
    console.log(this.the_prices_content)

    if (this.the_prices_content.length < 1) {

      if (this.the_prices_id != null) {
        console.log('从网络上获取内容')
      } else {
        console.log('无法加载' + this.the_prices_id)
      }

    } else {
      console.log('可正常展示：')
      console.log(this.the_prices_content)

      this.content = this.the_prices_content
    }
  },
  data() {
    return {
      content: []
    }
  },
  methods: {
    invoicePrint() {
      // tool.go()
    },
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"


</style>


