<template>
  <full-height-layout>

    <h3 slot="layout_title">报价单</h3>

    <div slot="layout_inner_head">

      <h5 class="fw-none mt-2">
        全部
      </h5>

      <prices-view-top></prices-view-top>

    </div>

    <!-- 表格主体 -->
    <transition v-if="isLoading" slot="layout_inner_body" name="fade">
      <loading></loading>
    </transition>

    <prices-view-body
      :prices="pricesList" 
      @pdfView_Father="pdfView" 
      @option_Father="option" 
      slot="layout_inner_body"

      v-else
    ></prices-view-body>

    <!-- 其它插件 -->
    <div slot="layout_extra">

      <trash @trashItem="_trash"></trash>


      <prices-pdf-modal 
        v-if="activePrices != null"

        :the_member="activePrices.membery" 
        :the_prices_timed="ser_timed(activePrices.start_time)" 
        :the_prices_numed="activePrices.num"

        :the_prices_content="[]"
        :the_prices_id="activeId"
      >
      </prices-pdf-modal>
      
    </div>

  </full-height-layout>
</template>

<script>
import view from '@/common/es/view'
import config from '@/common/config'
import tool from '@/common/es/tool'

import FullHeightLayout from '@/components/Layout/FullHeightLayout.vue'
import PricesViewTop from './Top/PricesViewTop.vue'

import conn from '@/service/conn'
import PricesViewBody from './Body/PricesViewBody.vue'

import Trash from '@/components/Modal/Trash.vue'
import Loading from '@/components/Shimmer/Loading.vue'

import PricesPdfModal from '@/components/Modal/Pdf/PricesPdfModal.vue'

export default {
  components: { FullHeightLayout, PricesViewTop, PricesViewBody, Loading, Trash, PricesPdfModal },
  name: 'PricesView',
  data() {
    return {
      pricesList: [],
      activeId: null,

      activePrices: null,

      isLoading: true
    }
  },
  mounted() {

    this.loadData()
  },
  methods: {
    loadData() {
      setTimeout(() => {
        conn.prices.getList_Prices({}, true).then(res => {
          this.pricesList = res
          this.isLoading = false
        })
      }, config.delayTime)
    },
    // 表格操作
    ser_timed(v) {return tool.timed.ser_timed(v, true, true) },
    option(id, option) {
      this.activeId = id

      if (option == 'update') {
       this.$router.push({
          path: '/home/member_update',
          query: {
            memberId: id
          }
        })
      } else if (option == 'trash') {
        tool.showModal('trashModal')
      }
    },
    pdfView(id) {
      this.activeId = id
      this.activePrices = this.pricesList.filter((e) => e.id == id)[0]
    },
    // 数据操作
    _trash() {
      const _this = this
      tool.closeModal('trashModal')

      if ((this.activeId != null) && (this.activeId > 0)) {

        conn.prices.trash(this.activeId).then(res => {
          try {
            const id = res['id']
            _this.isLoading = true
            _this.loadData()

          } catch(err) {

          }
        })

      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

</style>


