<template>
  <table class="table m-0 table-top-line-none">

    <tr class="text-left" v-for="(v, index) in prices" :key="v.id">
      <td width="6%" class="text-accent">{{ index + 1 }}</td>
      <td width="13%">{{ v.num }}</td>
      <td width="25%">{{ v.membery.num }}&nbsp;{{ v.membery.named }}</td>
      <td width="13%" class="text-center">{{ ser_timed(v.start_time) }}</td>
      <td width="13%" class="text-center">{{ v.freight_num }}</td>
      <td width="13%" class="text-center" @click="pdfView(v.id)">查看</td>
      
      <td width="12%" class="text-center">

        <i @click="option(v.id, 'trash')" class="float-right">
          <remove></remove>
        </i>

        <i @click="option(v.id, 'update')" class="float-right">
          <pencil></pencil>
        </i>

      </td>
    </tr>

  </table>
</template>

<script>
import view from '@/common/es/view'
import tool from '@/common/es/tool'

import Pencil from '@/components/Option/Pencil.vue'
import Remove from '@/components/Option/Remove.vue'

export default {
  name: 'PricesViewBody',
  components: { Pencil, Remove },
  props: {
    prices: {
      type: Array
    }
  },
  methods: {
    ser_timed(v) {return tool.timed.ser_timed(v, true, true) },
    option(id, option) { this.$emit('option_Father' , id, option) },
    pdfView(id) { this.$emit('pdfView_Father', id) }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


