<template>
    <table class="pdf-table fs-12 w-100 pdf-table-freight mt-2 text-center">
      <thead>
        <tr class="">
          <th width="79px">项号</th>
          <th>货号</th>
          <th class="text-left pl-4">描述</th>
          <th>单价</th>
        </tr>
      </thead>
      <tbody class="ptf-tbody">
        <tr v-for="(v, i) in freights" :key="v.id">
          <td>{{ i + 1 }}</td>
          <td>{{ v.num }}</td>
          <td class="text-left pl-4">{{ v.named }}</td>
          
          <td>
            <table class="prices-freight-price-inner-table text-left">
              <tr>
                <td class="text-right" width="40%">$</td>
                <td width="20%">{{ v.price }}</td>
                <td width="40%">/{{ unit(v.unit) }}</td>
              </tr>
            </table>
          </td> 
        </tr>
      </tbody>
    </table>
</template>

<script>
import view from '@/common/es/view'
import tool from '@/common/es/tool'

export default {
  name: 'PdfPricesFreight',
  props: {
    freights: {
      type: Array
    }
  },
  methods: {
    unit(u) {
      return view.ser_unit(u)
    },
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"

.pdf-table-freight
  // border: 1px solid #5a5a5a

.pdf-table-freight thead th
  padding: $horizonL
  // border: 1px solid #5a5a5a

.pdf-table-freight td
  // border-left: 1px solid #5a5a5a
  
.prices-freight-price-inner-table, .prices-freight-price-inner-table tr
  width: 100% !important

.prices-freight-price-inner-table td
  border: none !important

</style>


